import React, { Component } from 'react';
export default class Brick extends Component {
  render() {
    return (
      <React.Fragment>
        <article className="brick entry">
          <div className="entry__thumb">
            <a href="single-standard.html" className="thumb-link">
              <img src={this.props.article.mainImg} srcSet={this.props.article.imgSet} alt="" />
            </a>
          </div> {/* end entry__thumb */}
          <div className="entry__text">
            <div className="entry__header">
              <h1 className="entry__title"><a href="https://www.dreamhost.com/r.cgi?287326">{this.props.article.title}</a></h1>
              <div className="entry__meta">
                <span className="cat-links">
                  <a href="https://www.dreamhost.com/r.cgi?287326">{this.props.article.topics}</a>
                </span>
              </div>
            </div>
            <div className="entry__excerpt">
              <p>{this.props.article.description}</p>
            </div>
            <a className="entry__more-link" href={this.props.article.externalUrl}>Learn More</a>
          </div> {/* end entry__text */}
        </article> {/* end article */}
      </React.Fragment>
    );
  }
}